@import url(https://fonts.googleapis.com/css2?family=Barlow:ital,wght@1,300&family=Fredoka+One&family=Inter:wght@700&family=Mochiy+Pop+P+One&family=Poppins:ital,wght@1,300&family=Roboto:wght@300&display=swap);

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.shadow-sm{height:130px;width:100%;margin:0;padding:0}.shadow-sm a img{padding-left:80px;height:80px;position:absolute}.shadow-sm ul{display:flex;padding:30px 0px 0px 200px;width:100%}.shadow-sm ul li{list-style:none;margin-left:190px;transition:0.5s}.shadow-sm ul li a{font-family:"Inter", sans-serif;color:white;padding:10px;border-radius:4px;background-image:linear-gradient(135deg, #facb23 10%, #f55555 100%);text-decoration:none;font-size:20px}.shadow-sm ul li:hover{transform:scale(0.9)}.ButtonMenu{position:absolute;margin:5px 20px 0 0;transform:translateY(-155px) translateX(600px);display:none}.ButtonMenu img{width:60px;height:70px}.ResponseMenu{z-index:1000;transition:1s;position:fixed;text-align:center;padding-top:20%;opacity:0.8;top:15%;width:100%;height:100%;background-color:black;margin:0}.ResponseMenu ul{list-style:none}@media screen and (max-width: 1024px){.shadow-sm ul{padding:30px 0px 0px 100px}.shadow-sm ul li{margin-left:150px}}@media screen and (max-width: 768px){.shadow-sm ul{display:none}.ButtonMenu{display:block}}@media screen and (max-width: 425px){.shadow-sm a img{padding-left:40px}.ButtonMenu{transform:translateY(-155px) translateX(290px)}}@media screen and (max-width: 320px){.shadow-sm a img{padding-left:30px}.ButtonMenu{transform:translateY(-155px) translateX(220px)}}

.main{display:grid;grid-template-columns:50% 50%;height:70vh;padding-top:5vh}.main .container1{margin:100px 10px 0 50px}.main .container1 h2{font-size:50px;text-transform:uppercase;margin-bottom:10%;color:#13a513;font-family:"Fredoka One", cursive;line-height:4rem}.main .container1 article{text-transform:uppercase;font-family:"Inter", sans-serif;font-size:25px;color:white;line-height:3rem;background-color:orange;padding-right:30px;text-align:center;box-shadow:2px 2px 5px 0px orange}.main .container1 img{position:absolute;left:7%;top:65%;width:300px;height:250px}.main .container2 .img{text-align:center;position:absolute;width:900px;height:700px}@media screen and (max-width: 1440px){.main .container1 h2{font-size:40px}.main .container1 img{left:5%;top:70%;width:300px;height:250px}.main .container2 img{width:700px;height:600px}}@media screen and (max-width: 1024px){.main .container1{margin:80px 10px 0 50px}.main .container1 article{font-size:20px;line-height:40px}.main .container1 img{left:10%;top:75%;width:280px;height:200px}.main .container2 img{width:500px;height:550px}}@media screen and (max-width: 768px){.main{padding-top:10vh;height:70vh}.main .container1 h2{font-size:30px;text-align:center;line-height:3rem}.main .container1 article{font-size:18px;line-height:2rem}.main .container1 img{display:none}.main .container2 img{width:380px;height:500px}}@media screen and (max-width: 425px){.main{grid-template-columns:100%;padding-top:0}.main .container1{margin:0}.main .container1 h2{margin-bottom:5%}.main .container2 img{width:600px;height:300px}}

.mainContainer{display:grid;grid-template-columns:25% 25% 25% 25%;grid-row-gap:0;width:80%;height:60vh;margin:0 auto}.mainContainer .containers{width:350px;height:250px;overflow:hidden}.mainContainer .containers a{font-family:"Inter", sans-serif;position:absolute;color:black;text-transform:uppercase;z-index:100;padding:16rem 0 0 0;font-size:25px;width:300px}.mainContainer .containers .title{width:350px;height:250px;position:absolute;z-index:10;background-color:black;opacity:0.1}.mainContainer .containers img{transition:0.5s;width:350px;height:250px;z-index:-10}.mainContainer .containers:hover img{transform:scale(1.3)}.mainContainer .containers:hover .title{background-color:black;opacity:0.3}@media screen and (max-width: 1440px){.mainContainer{grid-column-gap:5%}.mainContainer .containers .title{width:300px}.mainContainer .containers img{width:300px}}@media screen and (max-width: 1024px){.mainContainer{grid-column-gap:10%}.mainContainer .containers .title{width:260px}.mainContainer .containers img{width:260px}}@media screen and (max-width: 768px){.mainContainer{margin-left:3%;grid-column-gap:15%}.mainContainer .containers{width:250px}.mainContainer .containers img{width:240px}.mainContainer .containers a{width:250px}.mainContainer .containers:hover img{transform:scale(1)}.mainContainer .containers:hover .title{background-color:black;opacity:0.5}}@media screen and (max-width: 425px){.mainContainer{grid-template-columns:100%;margin:0 auto}.mainContainer .containers{width:300px;margin-bottom:50px;text-align:center}.mainContainer .containers a{text-align:center;width:300px}.mainContainer .containers .title{width:300px}.mainContainer .containers img{width:300px}.mainContainer .containers:hover img{transform:scale(1)}.mainContainer .containers:hover .title{background-color:black;opacity:0.5}}

.main-content{margin:0 auto;width:150vh}.image{text-align:center;height:400px;background-position:center}.image img{height:100%;width:780px;overflow:hidden}.content{margin-top:5px;display:grid;grid-template-columns:50% 50%;text-align:center}.content .content1{width:100%;font-size:20px;text-align:left}.content .content1 img{height:40px;margin:0 15px 0 15px}.content .content1 ul{list-style-type:none;background-color:#f7f7f7;width:70%}.content .content1 ul h3{color:#f7903b}.content .content1 ul li{margin-bottom:20px;font-size:30px}.content .content1 ul li:first-child{margin-top:20px}.content .content1 input{margin-right:15px;width:20px;height:20px}.content .content2{text-align:left}.content .content2 .title{padding-bottom:10px;width:100%;margin:15px 0 25px 0;border-bottom:2px solid #f7903b;background-color:#f7f7f7}.content .content2 article{font-size:20px}@media screen and (max-width: 768px){.main-content{width:100%}.main-content .image{text-align:center;background-position:left}.main-content .image img{height:100%;width:90%;overflow:hidden}.main-content .content{grid-template-columns:90%}.main-content .content .content1,.main-content .content .content2{transform:translateX(5%)}}@media screen and (max-width: 425px){.main-content .image img{height:100%;width:90%;overflow:hidden}.main-content .content{grid-template-columns:90%}.main-content .content .content1 ul{width:100%}.main-content .content .content1 ul li{margin-bottom:20px;font-size:20px}.main-content .content .content2{transform:translateX(5%);width:100%}.main-content .content .content2 article{font-size:15px}}

